@import url(https://fonts.googleapis.com/css2?family=Pacifico&display=swap);
body {
  margin: 0;
  font-family: "Poppins", sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

* {
  margin: 0;
  padding: 0;
}

* {
  padding: 0px;
  margin: 0px;
  box-sizing: border-box;
  font-family: "Poppins", sans-serif;
}
:root {
  --red: #d70049;
  --blue: #032656;
  --lblue: #50759f;
  --pink: #da0b4e;
  --lpink: #de2460;
  --black: #2d3954;
  --green: #00ba74;
  --lgray: #b2afad;
  --gray: #75767a;
  --dblue: #291b4f;
  --yellow: #ffca08;
}
.navbar {
  background: #fff;
  height: 70px;
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
  font-size: 1.2rem;
  border-bottom: 1px solid #b2afad;
  border-bottom: 1px solid var(--lgray);
  background-color: #3e4095;
  -webkit-clip-path: polygon(10% 0, 90% 0, 80% 100%, 19% 100%);
          clip-path: polygon(10% 0, 90% 0, 80% 100%, 19% 100%);
}

.navbar-container {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
  height: 90px;
  max-width: 1500px;
}

