.nav-menu {
  display: grid;
  grid-template-columns: repeat(15, auto);
  /* grid-gap: 10px; */
  list-style: none;
  text-align: center;
  -webkit-justify-content: end;
          justify-content: end;
  /* margin-right: 2rem; */
}

.nav-item {
  height: 80px;
  color: #000;
}
.nav-item-button {
  display: none;
}
.nav-links {
  color: #fff;
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
  text-decoration: none;
  padding: 0.9rem 2rem;
  font-size: 1rem;
}

.nav-links:hover {
  color: #e52165;
  border-bottom: 4px solid #fff;
  transition: all 0.2s ease-out;
  text-decoration: none;
}

.fa-bars {
  color: #fff;
}

.nav-links-mobile {
  display: none;
}

.menu-icon {
  display: none;
}
.icon {
  display: none;
}
@media screen and (max-width: 960px) {
  .navbar {
    height: 80px;
    -webkit-clip-path: none;
            clip-path: none;
    background-color: #fff;
  }
  .NavbarItems {
    position: relative;
  }

  .nav-menu {
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    width: 100%;
    max-height: 90vh;
    position: absolute;
    top: 80px;
    left: -80%;
    opacity: 0.2;
    transition: all 0.5s ease;
    overflow-y: scroll;
    text-align: left;
  }

  .nav-menu.active {
    background: #3e4095;
    left: 0;
    opacity: 0.9;
    transition: all 0.5s ease;
    z-index: 1;

    /* border-right: 2px solid var(--lgray);
    border-top: 2px solid var(--lgray);
    border-bottom: 2px solid var(--lgray); */
    border-radius: 5px;
    overflow-y: scroll;
  }
  .nav-item {
    height: -webkit-max-content;
    height: max-content;
    position: relative;
    right: 10px;
  }
  .nav-links {
    /* text-align: center; */
    /* align-items: center; */
    /* text-align: center; */
    /* padding: 1rem 0.5rem; */
    width: 100%;
    /* display: block; */
    color: #fff;
    overflow-y: scroll;
    font-size: 16px;
  }

  .nav-links:hover {
    /* background-color: #fff; */
    color: #242424;
    border-radius: 0;
  }

  .navbar-logo {
    width: 200px;
    height: 90px;
  }
  .navbar-logo img {
    height: 60px;
    width: 100%;
    position: relative;
    right: 10%;
    display: block;
  }
  .menu-icon {
    display: block;
    position: absolute;
    top: 0;
    right: 0;
    -webkit-transform: translate(-100%, 60%);
            transform: translate(-100%, 60%);
    font-size: 1.8rem;
    cursor: pointer;
  }
  .fa-bars {
    color: #3e4095;
    font-size: 1.8rem;
  }
  .fa-times {
    color: #3e4095;
    font-size: 1.8rem;
  }
  .nav-item {
    text-align: center;
  }
  .nav-item-button {
    display: block;
  }

  .nav-links-mobile {
    display: block;
    text-align: center;
    margin: 2rem auto;
    border-radius: 4px;
    width: 80%;
    text-decoration: none;
    font-size: 1rem;
    background-color: transparent;
    color: #fff;
    padding: 14px 20px;
    /* border: 1px solid #fff; */
    transition: all 0.3s ease-out;
  }
  .icon {
    display: block;
    margin-bottom: 1rem;
  }
  .icon i {
    background-color: #b2afad;
    background-color: var(--lgray);
    border-radius: 50%;
    padding: 1rem;
    color: #fff;
    margin-left: 1rem;
  }
  .nav-links-mobile:hover {
    background: #fff;
    color: #242424;
    transition: 250ms;
  }
}

:root {
  --primary: red;
}

.btn {
  padding: 8px 20px;
  border-radius: 2px;
  outline: none;
  border: none;
  cursor: pointer;
}

.btn--primary {
  background-color: red;
  background-color: var(--primary);
  color: #242424;
  border: 1px solid red;
  border: 1px solid var(--primary);
}

.btn--outline {
  background-color: transparent;
  color: #fff;
  padding: 8px 20px;
  border: 1px solid red;
  border: 1px solid var(--primary);
  transition: all 0.3s ease-out;
  display: none;
}
.btn--medium {
  padding: 8px 20px;
  font-size: 20px;
}
.btn--large {
  padding: 12px 26px;
  font-size: 20px;
}
.btn--medium:hover,
.btn--large:hover {
  background-color: #fff;
  color: #242424;
  transition: all 0.3s ease-out;
}

.container-slider {
  height: 420px;
  position: relative;
  overflow: hidden;
  box-shadow: 0 10px 20px rgba(0, 0, 0, 0.2);
}
@media screen and (max-width: 700px) {
}
.slide {
  width: 100%;
  height: 100%;
  position: absolute;
  opacity: 0;
  transition: opacity ease-in-out 0.4s;
}
.slide img {
  width: 100%;
  height: 100%;
  object-fit: cover;
}
.active-anim {
  opacity: 1;
}

.btn-slide {
  width: 60px;
  height: 60px;
  border-radius: 50%;
  background: #f1f1f1;
  border: 1px solid rgba(34, 34, 34, 0.287);
  position: absolute;
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
  cursor: pointer;
}

.btn-slide img {
  width: 25px;
  height: 25px;
  pointer-events: none;
}
.prev {
  top: 50%;
  left: 20px;
  -webkit-transform: translateY(-60%);
          transform: translateY(-60%);
}
.next {
  top: 50%;
  right: 20px;
  -webkit-transform: translateY(-60%);
          transform: translateY(-60%);
}

.container-dots {
  position: absolute;
  bottom: 10px;
  left: 50%;
  -webkit-transform: translateX(-50%);
          transform: translateX(-50%);
  display: -webkit-flex;
  display: flex;
}
.dot {
  width: 20px;
  height: 20px;
  border-radius: 50%;
  border: 3px solid #f1f1f1;
  margin: 0 5px;
  background: #f1f1f1;
}
.dot.active {
  background: rgb(32, 32, 32);
}

